import React,{useState, useRef, useEffect} from 'react';
import {CountdownCircleTimer} from "react-countdown-circle-timer";
import './timer.css'
import styled from 'styled-components';
import {Button} from 'react-bootstrap';
import alarm from './timer.mp3'






const Container = styled.div`
width: 100%;
height: 100%;
min-width: 1100px;
background: skyblue;

`

const Wrapper = styled.div`
width: ${((props)=>props.width)}px;
height: ${((props)=>props.width)}px;
position: relative;
transform: translateX(-50%);
margin-left:50%;
`



function Timer() {
 


const [width, setWidth] = useState(window.innerWidth);
const [height, setHeight] = useState(window.innerHeight);
const radius = Math.min(width,height)
const [isStarted,setIsStarted] = useState(false)
const [key,setKey] = useState(0)


const audio = new Audio(alarm);





//start, pause 버튼
const clickToggle = () => {
  
  setIsStarted(!isStarted)
  if(isReset){
    setIsReset(!isReset)
    
  }
  
}


//reset버튼
const [isReset,setIsReset] = useState(true);
const resetToggle = () =>{
  if(!isNaN(seconds)){
  setIsReset(!isReset);
  setIsStarted(false)
  setKey(key+1);
  // audio.pause();
  
}
}

//시간설정
const [seconds,setSeconds] = useState(60)



window.addEventListener('resize',function(){
  setWidth(window.innerWidth)
  setHeight(window.innerHeight)
})
  return(
    <Container>
       
      <Wrapper height={radius} width={radius} color='red'>
        <div className="timer-wrapper">
        <CountdownCircleTimer
          isPlaying={isStarted}
          duration={parseInt(seconds)}
          colors={[["#004777", 0.33], ["#F7B801", 0.33], ["#A30000"]]}
          onComplete={() =>{audio.play()
          return [false, 1000]}}
          size={radius}
          strokeWidth={40}
          key={key}
        >
          {({remainingTime})=>{
            // if (remainingTime === 0) {
            //   return <div>Time's up</div>;
            // }
            if (remainingTime === 0) {
                // audio.play()
              
              
              return (
                <div>
                  <div className="timer">Time's up</div>
                  <Button variant="danger"  className="btnReset" onClick={()=>{resetToggle()}}>Reset</Button>
                </div>
              )

                

            }
            
            return (
              <div>
              {isReset //리셋상태일때
              ? <div><input placeholder="시간(초) 입력" className="btnInput" onChange={(e)=>{setSeconds(e.target.value)}}/>
              <Button variant="danger" onClick={()=>{resetToggle()}}>Input</Button></div>
            : <div>
              <div className="remainingTime">{remainingTime}</div>
              {isStarted
              ? <Button variant="danger" className="btnPause" onClick={()=>{clickToggle()}}>Pause</Button>
              : <Button variant="danger" className="btnStart" onClick={()=>{clickToggle()}}>Start</Button>}
              <Button variant="danger"  className="btnReset" onClick={()=>{resetToggle()}}>Reset</Button>
              </div> 
              
          }</div>)}}
            
          
        </CountdownCircleTimer>
        
        {/* <button onClick={()=>{audio.play()}}>test</button> */}
         
        </div>
        
      </Wrapper>
    </Container>
  )
}






export default Timer;